.App{
  display: flex;
  justify-content: center;
}
.calculator{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 200px;
}
.calculator > *{
  border: 1px solid black;
  padding: 10px;
}
.row{
  grid-column: 1 / -1;
}
#display{
  text-align: right;
}
#clear{
  text-align: center;
}